.sendForm {
    width: 100%;
    height: 100%;
    display: flex;
    padding-top: 56px;
    justify-content: space-between;
}

.textarea {
    font-size: 14px;
    padding: 12px 24px;
    flex-grow: 1;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
}

.button {
    width: 120px;
    height: 40px;
    background: #0066CC;
    border-radius: 20px;
    margin-left: 46px;
}

